// 判断 输入的 账户密码 是否 和 vuex 中的数据相等 并且不能为空
let userInfoDetect = (data, userAccount, userPassword) => {
  if (userPassword === '' || userAccount === '') {
    return false
  }
  for (let i = 0; i < data.length; i++) {
    // console.log('data[i].password = ', data[i].password)
    if (data[i].password === userPassword && data[i].account === userAccount) {
      return true
    }
  }
  return false
}

// 从注册用户用找出登录用户的下标
let getIndex = (obj, userAccount) => {
  let index = -1
  for (let i in obj) {
    // console.log(i)
    if (userAccount === obj[i].account) {
      index = i
      break
    }
  }
  return index
}
export {
  userInfoDetect,
  getIndex
}
