<template>
  <div class="row-bg" style="width: 60%; margin-left: 18%; margin-top: 5%">
    <el-row type="flex" justify="center">
      <el-col :span="18">
        <div style="text-align:center;">
          <h1 style="font-size: 22px">个人中心</h1>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center">
      <el-col :span="18">
        <div style="text-align: center;">
          <div class="addInfoTag">&nbsp;&nbsp;&nbsp;姓名</div>
          <!--<el-input style="width: 40%; margin-bottom: 15px;" v-model="name"></el-input>-->
          <el-tag style="width: 40%; font-size: 15px">{{ this.name }}</el-tag>
          <br>
          <div class="addInfoTag">用户名</div>
          <!--<el-input style="width: 40%; margin-bottom: 15px;" v-model="phone"></el-input>-->
          <el-tag style="width: 40%; font-size: 15px">{{ this.account }}</el-tag>
          <br>
          <div class="addInfoTag" style="margin-bottom: 30px;">&nbsp;&nbsp;&nbsp;密码</div>
          <el-tag style="width: 40%; font-size: 15px">{{ this.password }}</el-tag>
          <br>
          <el-button @click="changePassword">修改密码</el-button>
          <el-button style="margin-bottom: 20px" @click="signOut">退出登录</el-button>
        </div>
        <!--弹出框-->
        <el-dialog title="修改密码" :visible.sync="dialogFormVisible">
          <el-form>
            <el-form-item label="旧密码">
              <el-input v-model="oldPassword"></el-input>
            </el-form-item>
            <el-form-item label="新密码">
              <el-input v-model="newPassword"></el-input>
            </el-form-item>
          </el-form>
          <div>
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" @click="determine">确 定</el-button>
          </div>
        </el-dialog>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    name: 'personalCenter',
    data () {
      return {
        oldPassword: '',
        newPassword: '',
        name: localStorage.getItem('name'),
        account: JSON.parse(localStorage.getItem('registeredUser'))[0].account,
        password: '*******',
        dialogFormVisible: false
      }
    },
    methods: {
      // 修改密码
      changePassword () {
        this.dialogFormVisible = true
      },
      // 修改密码弹出框的取消按钮
      cancel () {
        console.log('取消')
        this.dialogFormVisible = false
        this.oldPassword = ''
        this.newPassword = ''
        this.$message({
          message: '取消修改密码',
          type: 'warning'
        })
      },
      // 修改密码弹出框的确定按钮
      determine () {
        console.log('修改', this.oldPassword, this.newPassword)
        // 获得存在 localStorage 中所有的注册用户
        let user = JSON.parse(localStorage.getItem('registeredUser'))
        // 获得存在 localStorage 中的 name
        let name = localStorage.getItem('name')
        // 获得 当前登录用户的 下标
        let index = -1
        console.log(index)
        for (let i in user) {
          if (name === user[i].name) {
            index = Number(i)
            break
          }
        }
        if (this.oldPassword !== user[index].password) {
          // console.log('密码错误')
          this.$message({
            message: '旧密码错误！',
            center: true,
            type: 'error'
          })
        } else {
          // console.log('旧密码验证成功！')
          if (this.newPassword.length === 0 || this.newPassword.length > 11 ||this.newPassword.length < 6) {
            this.$message({
              message: '新密码长度必须大于等于 6 并且小于 11',
              center: true,
              type: 'error'
            })
          } else {
            this.$message({
              message: '修改密码成功',
              type: 'success'
            })
            let o = {
              name: user[index].name,
              account: user[index].account,
              password: this.newPassword
            }
            // 修改 localStorage 中这个用户的密码 并更新
            user[index] = o
            localStorage.setItem('registeredUser', JSON.stringify(user))
            this.dialogFormVisible = false
          }
        }
      },
      // 退出登录
      signOut () {
        console.log('退出')
        sessionStorage.removeItem('login')
        this.$router.push({ path: '/login' })
      }
    }
  }
</script>

<style>
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
    border-radius: 26px;
  }
  .addInfoTag {
    width: 80px;
    height: 30px;
    margin-right: 0px;
    text-align: left;
    line-height: 50px;
    overflow: inherit;
    display: inline-block;
  }
</style>
