import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './plugins/element.js'

Vue.config.productionTip = false

// 导航守卫，根据是否登录而路由 （暂时没有找到更好的办法 进行 子路由的拦截。。 只能进行一个一个的判断）
router.beforeEach((to, from, next) => {
  if (to.path === '/index') {
    if (sessionStorage.getItem('login') === '1') {
      next()
    } else {
      next('/login')
    }
  } else if (to.path === '/index/list') {
    if (sessionStorage.getItem('login') === '1') {
      next()
    } else {
      next('/login')
    }
  } else if (to.path === '/index/add') {
    if (sessionStorage.getItem('login') === '1') {
      next()
    } else {
      next('/login')
    }
  } else if (to.path === '/index/center') {
    if (sessionStorage.getItem('login') === '1') {
      next()
    } else {
      next('/login')
    }
  } else {
    next()
  }
})
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
