<template>
  <el-col :span="24">
    <el-input v-model="account" placeholder="请输入账号" style="width: 30%; margin-bottom: 0.5rem"></el-input>
    <br>
    <el-input v-model="password" placeholder="请输入密码" show-password style="width: 30%; margin-bottom: 1rem"></el-input>
    <br>
    <el-button @click="login">登录</el-button>
  </el-col>
</template>

<script>
import { userInfoDetect, getIndex } from '../componentsMethod/userInfoDetection'
export default {
  name: 'login',
  data () {
    return {
      account: '',
      password: '',
      test: true
    }
  },
  computed: {
  },
  methods: {
    login () {
      // 点击登录的时候 判断 vuex 中的 account 是否和输入的匹配
      // 如果 正确 就进行下一步判断 密码是否和输入的匹配
      // 都正确 就登录成功

      // 获得存在 localStorage 中所有的注册用户
      let user = JSON.parse(localStorage.getItem('registeredUser'))
      // 判断输入的账户密码和 localStorage 中存储的数据是否相等
      let password = userInfoDetect(user, this.account, this.password)
      if (password) {
        this.$message({
          message: '登录成功',
          center: true,
          type: 'success'
        })
        // 获取登录用户的昵称
        localStorage.setItem('name', user[getIndex(user, this.account)].name)
        // 往 sessionStorage 存入 登录 信息
        sessionStorage.setItem('login', '1')
        this.$router.push({ path: '/index' })
      } else {
        this.$message({
          message: '用户名或密码错误',
          center: true,
          type: 'error'
        })
      }
    }
  }
}
</script>

