<style>
  .select {
    float: right;
    margin-top: 10px;
    margin-right: 15px;
  }
  .selectText {
    color: #000 !important;
  }
  .el-radio__input.is-checked+.el-radio__label {
    color: #ffcf15 !important;
  }
  .el-radio__input.is-checked .el-radio__inner {
    border-color: #ffcf15 !important;
    background: #ffcf15 !important;
  }
</style>
<template>
  <el-container>
    <el-header>
      <el-row>
        <el-col :span="12">
          <el-input v-model="input" placeholder="请输入搜索联系人" style="width: 30%"
                    @input="search">
            <i
              class="el-icon-search el-input__icon"
              slot="suffix">
            </i>
          </el-input>  共有 {{ this.tableData.length }} 个联系人
        </el-col>
        <el-col :span="12">
          <div class="select">
            <el-radio-group v-model="radio" @change="selectRadio">
              <el-radio :label="0" class="selectText">所有联系人</el-radio>
              <el-radio :label="1" class="selectText">家人</el-radio>
              <el-radio :label="2" class="selectText">朋友</el-radio>
              <el-radio :label="3" class="selectText">同学</el-radio>
            </el-radio-group>
          </div>
        </el-col>
      </el-row>
    </el-header>
    <el-main>
      <el-row type="flex" justify="center">
        <el-col :span="24">
          <el-table
            :data="tableData.slice((currentPage-1)*pageSize,currentPage*pageSize)"
            style="width: 70%; border-radius: 15px">
            <el-table-column
              label="关系"
              width="180"
              align="center">
              <template slot-scope="scope">
                <el-input v-if="scope.row.status" v-model="snapConnections"></el-input>
                <span style="margin-left: 5px" v-else>{{ scope.row.connections }}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="姓名"
              width="180"
              align="center">
              <template slot-scope="scope">
                <el-input v-if="scope.row.status" v-model="snapName"></el-input>
                <el-tag v-else>{{ scope.row.name }}</el-tag>
              </template>
            </el-table-column>
            <el-table-column
              label="联系方式"
              align="center">
              <template slot-scope="scope">
                <el-input v-if="scope.row.status" v-model="snapPhone"></el-input>
                <span v-else>{{ scope.row.phone }}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="操作"
              align="center">
              <template slot-scope="scope">
                <el-button size="mini" v-if="scope.row.status" @click="determine(scope.$index, scope.row)">完成</el-button>
                <el-button size="mini" v-if="scope.row.status" @click="cancelEditing(scope.$index, scope.row)">取消</el-button>
                <el-button
                  size="mini"
                  @click="handleEdit(scope.$index, scope.row)"
                  v-else>编辑</el-button>
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-pagination
            style="margin-top: 15px"
            background
            :page-size="pageSize"
            :current-page.sync="currentPage"
            layout="prev, pager, next"
            :total="total"
            @current-change="current_change">
          </el-pagination>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: 'peopleList',
  data () {
    return {
      // 表格数据 一共*条
      total: 0,
      // 表格每页 5 条数据
      pageSize: 5,
      // 分页的初始值， 第一页
      currentPage: 1,
      // input 过滤
      input: '',
      // 单选框的选中下标
      radio: 0,
      // 表格数据
      tableData: JSON.parse(localStorage.getItem('peopleList')),
      // 表格数据副本（进行过滤操作，并且不影响原数据）
      tableData1: [],
      // 存放 input 中的 value 值
      snapConnections: '',
      snapName: '',
      snapPhone: ''
    }
  },
  created () {
    // 获得 vuex 的数据 存入 localStorage 中
    let status = localStorage.hasOwnProperty('peopleList')
    if (status === false) {
      this.tableData = this.$store.state.addressBookList
      localStorage.setItem('peopleList', JSON.stringify(this.tableData))
    }
    // 深拷贝
    this.tableData1 = JSON.parse(JSON.stringify(this.tableData))
    // 获得 表格的长度
    this.total = this.tableData.length
  },
  computed: {
    // 返回一个 布尔 来查看 所有列表的编辑状况
    getStatus () {
      let number = 0
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].status === true) {
          number += 1
        }
      }
      return number
    }
  },
  methods: {
    // input 过滤
    search () {
      // console.log('aaaa', this.radio)
      this.tableData = this.tableData1.filter(data => !this.input || data.name.toLowerCase().includes(this.input.toLowerCase()))
      // input 为空时 先需要判断 单选框的状态（所有人 家人 朋友 同学） 以便 input 值清空时 恢复到相应的 单选框筛选列表
      this.currentPage = 1
      if (this.input === '') {
        switch (this.radio) {
          case 0:
            // console.log('所有人')
            this.tableData = this.tableData1
            break
          case 1:
            // console.log('家人')
            this.tableData = this.tableData1.filter(data => !'家人' || data.connections.toLowerCase().includes('家人'.toLowerCase()))
            break
          case 2:
            // console.log('朋友')
            this.tableData = this.tableData1.filter(data => !'朋友' || data.connections.toLowerCase().includes('朋友'.toLowerCase()))
            break
          case 3:
            // console.log('同学')
            this.tableData = this.tableData1.filter(data => !'同学' || data.connections.toLowerCase().includes('同学'.toLowerCase()))
            break
        }
      }
    },
    // 单选框过滤
    selectRadio (label) {
      switch (label) {
        case 0:
          // console.log('所有人')
          this.currentPage = 1
          this.tableData = this.tableData1
          break
        case 1:
          // console.log('家人')
          this.currentPage = 1
          this.tableData = this.tableData1.filter(data => !'家人' || data.connections.toLowerCase().includes('家人'.toLowerCase()))
          break
        case 2:
          // console.log('朋友')
          this.currentPage = 1
          this.tableData = this.tableData1.filter(data => !'朋友' || data.connections.toLowerCase().includes('朋友'.toLowerCase()))
          break
        case 3:
          // console.log('同学')
          this.currentPage = 1
          this.tableData = this.tableData1.filter(data => !'同学' || data.connections.toLowerCase().includes('同学'.toLowerCase()))
          break
      }
    },
    // 编辑
    handleEdit (index, row) {
      // console.log('test', this.getStatus)
      // 判断编辑状态 列表每次只能有一条数据 触发编辑状态
      if (this.getStatus > 0) {
        this.$message({
          message: '请取消上一个编辑状态',
          center: true,
          type: 'error'
        })
        row.status = false
      } else {
        row.status = true
        this.snapConnections = row.connections
        this.snapName = row.name
        this.snapPhone = row.phone
      }
    },
    // 取消编辑
    cancelEditing (index, row) {
      row.status = false
    },
    // 完成编辑
    determine (index, row) {
      // console.log(row)
      row.status = false
      // bug!!
      // console.log(peopleList[index], '对应下标', index)
      let pageEntry_index = this.pageSize * (this.currentPage - 1) + index
      this.tableData[pageEntry_index].connections = this.snapConnections
      this.tableData[pageEntry_index].name = this.snapName
      this.tableData[pageEntry_index].phone = this.snapPhone
      let peopleList = JSON.parse(localStorage.getItem('peopleList'))
      peopleList[pageEntry_index] = {
        connections: this.snapConnections,
        name: this.snapName,
        phone: this.snapPhone
      }
      // console.log('页码', this.currentPage, index)
      // console.log('>>>>>>>>更改后<<<<<<<<<', peopleList)
      localStorage.setItem('peopleList', JSON.stringify(peopleList))

    },
    // 删除
    handleDelete (index, row) {
      console.log(index, row)
      let pageEntry_index = this.pageSize * (this.currentPage - 1) + index
      this.tableData.splice(pageEntry_index, 1)
      let peopleList = JSON.parse(localStorage.getItem('peopleList'))
      peopleList.splice(pageEntry_index, 1)
      localStorage.setItem('peopleList', JSON.stringify(peopleList))
    },
    // 分页
    current_change (currentPage) {
      // console.log(currentPage)
      this.currentPage = currentPage
      console.log(this.total)
    }
  }
}
</script>

<style scoped>

</style>
