<template>
  <div class="row-bg" style="width: 60%; margin-left: 18%; margin-top: 5%">
    <el-row type="flex" justify="center">
      <el-col :span="18">
        <div style="text-align:center;">
          <h1 style="font-size: 22px">联系人</h1>
        </div>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center">
      <el-col :span="18">
        <div style="text-align: center;">
          <div class="addInfoTag">姓名</div>
          <el-input style="width: 40%; margin-bottom: 15px;" v-model="name"></el-input>
          <br>
          <div class="addInfoTag">联系电话</div>
          <el-input style="width: 40%; margin-bottom: 15px;" v-model="phone"></el-input>
          <br>
          <div class="addInfoTag">关系</div>
          <el-select style="width: 40%; margin-bottom: 30px;" v-model="connections" placeholder="请选择关系">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <br>
          <el-button @click="contactAdd" style="margin-bottom: 20px">添加联系人</el-button>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'infoAdd',
  data () {
    return {
      name: '',
      phone: '',
      connections: '',
      options: [{
        value: '家人',
        label: '家人'
      }, {
        value: '朋友',
        label: '朋友'
      }, {
        value: '同学',
        label: '同学'
      }]
    }
  },
  methods: {
    contactAdd () {
      if (this.name.length >= 5 || this.name.length === 0) {
        this.$message({
          message: '姓名不能为空并且少于4个字',
          center: true,
          type: 'error'
        })
      } else {
        if (this.phone.length === 0 || this.phone.length > 11 || this.phone.length < 9) {
          this.$message({
            message: '联系电话最少需要9个数并且少于11个数',
            center: true,
            type: 'error'
          })
        } else {
          if (this.connections === '') {
            this.$message({
              message: '请选择与该联系人的关系',
              center: true,
              type: 'error'
            })
          } else {
            this.$message({
              message: '添加联系人成功',
              center: true,
              type: 'success'
            })
            let o = {
              connections: this.connections,
              name: this.name,
              phone: this.phone,
              status: false
            }
            let peopleList = JSON.parse(localStorage.getItem('peopleList'))
            peopleList.push(o)
            localStorage.setItem('peopleList', JSON.stringify(peopleList))
          }
        }
      }
    }
  }
}
</script>

<style>
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
    border-radius: 26px;
  }
  .addInfoTag {
    width: 80px;
    height: 30px;
    margin-right: 10px;
    text-align: right;
    line-height: 50px;
    overflow: inherit;
    display: inline-block;
  }
</style>
